// 
export const putUserSelf = async (id, formData) => {
  try {
    // content type + header
    const res = await axiosInstance.put(`${baseUrl}/users/${id}`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return res;
  } catch (error) {
    console.error("[Put user failed]", error.response.data.message);
    return error;
  }
};

