import AuthButton from 'components/Form/AuthButton.jsx'
import AuthInput from 'components/Form/AuthInput.jsx'
import ac_logo from 'icons/ac_logo.svg'
import styles from './LoginPage.module.scss'
import clsx from 'clsx'
import { Link } from 'react-router-dom';
import { useState, useEffect, useContext } from 'react'
import { useNavigate } from 'react-router-dom';
import { AuthContext } from 'context/AuthContext.jsx'


export default function LoginPage() {
  const [account, setAccount] = useState('')
  const [password, setPassword] = useState('')
  // errorMsgresponse<AuthInput/>
  const [errorMsg, setErrorMsg] = useState('')
  const navigate = useNavigate();
  const { login, isAuthenticated } = useContext(AuthContext);

  const handleClick = async () => {
    // 
    if (account.length === 0 || password.length === 0) return
    const response = await login({ account, password })
    //
    if(!response.data) {
      if (response.response.data.status === "error") return setErrorMsg(response.response.data.message)
    }
    //
  }
  useEffect(() => {
    if (isAuthenticated) {
      navigate('/main');
    }
  }, [navigate, isAuthenticated])
  return (
    <div className={styles.loginContainer}>
      <img src={ac_logo} alt="ac_logo.svg" />
      <h3 className={styles.authTitle}>{" Alphitter"}</h3>
      <AuthInput
        className={styles.account}
        borderLine={clsx({ [styles.borderLine]: !errorMsg.length }, { [styles.accountBorderLineError]: errorMsg === "Error: " || errorMsg === "Error: " || errorMsg === "" })}
        label={""}
        placeholder={""}
        value={account}
        onChange={(accountInput) => {
          setErrorMsg('')
          setAccount(accountInput)
        }} />
      <AuthInput
        className={styles.password}
        borderLine={clsx({ [styles.borderLine]: !errorMsg.length }, { [styles.invalidUserBorderLine]: errorMsg === "Error: " || errorMsg === "Error: " || errorMsg === "" })}
        label={""}
        placeholder={""}
        type="password"
        value={password}
        onChange={(passwordInput) => {
          setErrorMsg('')
          setPassword(passwordInput)
        }} />
      <AuthButton btn="" onClick={handleClick} />
      <div className={styles.redirContainer}>
        <Link to="/signup">
          <span className={styles.signup}></span>
        </Link>
        <span className={styles.dot}></span>
        <Link to="/admin">
          <span className={styles.admin}></span>
        </Link>
      </div>
    </div>
  )
}